import axios from 'axios'
import ajaxConf from './ajaxConf'
axios.defaults.headers.post['Content-Type'] = 'application/json';

  // 1GPS定位信息上传接口
  // {data: params, 'Content-Type':'text/plain', 'Access-Control-Allow-Origin': *, 'Access-Control-Allow-Headers': 'authorization'}
  export const SendPoint = params => { return axios.post(ajaxConf.SEND_POINT_URL, params) }
  
  // 2二维码验票接口
  export const GetQrCodeChk = params => { return axios.post(ajaxConf.GET_QRCODE_CHK_URL, params) }
  
  // 3手动验票接口
  export const GetHandChk = params => { return axios.post(ajaxConf.GET_HAND_CHK_URL, params) }
  
  // 统一验票接口，不用上面两个
  export const GetTicketChk = params => { return axios.post(ajaxConf.GET_TICKET_CHK_URL, params) }
  
  // 3.1获取票务信息接口
  export const QueryTicketInfos = params => { return axios.post(ajaxConf.QUERY_TICKET_INFOS_URL, params) }
  
  // 4获取验票班次接口
  export const QueryChkShifts = params => { return axios.post(ajaxConf.QUERY_CHK_SHIFTS_URL, params) }
  
  // 4.1获取当前验票班次接口
  export const QueryRecenShifts = params => { return axios.post(ajaxConf.QUERY_LATEST_SHIFTS_URL, params) }
  
  // 5获取站点即使验票情况接口
  export const QueryStationChk = params => { return axios.post(ajaxConf.QUERY_STATION_CHK_URL, params) }
  
  // 6获取当前站点未验票乘客数据接口
  export const QueryUnchkedList= params => { return axios.post(ajaxConf.QUERY_UNCHKED_LIST_URL, params) }
  
  // 7获取班次统计接口
  export const QueryShiftsStatis = params => { return axios.post(ajaxConf.QUERY_SHIFTS_STATIS_URL, params) }
 
  // 退出系统
  export const GetLoginOut = params => { return axios.post(ajaxConf.GET_LOGINOUT_URL, params) }
  
  // 登录系统
  export const GetLogin = params => { return axios.post(ajaxConf.GET_LOGIN_URL, params) }


