
  import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

  export default {
    components: {
      XHeader,
      Toast,
      Loading
    },
    name: 'm-header',
    data () {
      return {
        showQuit: false,
        time: 2000
      }
    },
    methods: {
      onClickBack () {
        const _this = this;
        switch (_this.$route.path) {
          case '/user/basicInfo': _this.$router.push('/user')
            break;
          default: this.$router.go(-1)
            break;
        }
      }
    },
    computed: {
      title () {
        // 监听返回按钮
        let i = 0
        const that = this
        setTimeout(() => {
        	window.plus.key.removeEventListener("backbutton",onback);
        	window.plus.key.addEventListener("backbutton",onback);
          function onback () {
	          	if(that.$route.path === '/user/login') {
	          		window.plus.runtime.quit()
	          		return
	          	}
	            if (that.$route.path === '/' || that.$route.path === '/steward' || that.$route.path === '/user') {
	              i++
	              if (i >= 2) {
	                window.plus.runtime.quit()
	              } else {
	                that.showQuit = true
	                setTimeout(() => {
	                  i = 0
	                }, 2000)
	              }
	            } else {
	              that.$router.go(-1)
	            }
          }
        }, 500)
        return this.$store.state.comm.indexConf.title
      },
      isBack () {
        return this.$store.state.comm.indexConf.isBack
      },
      loading () {
        return this.$store.state.comm.loading
      }
    },
    created () {
      // 调用native api
      const plusReady = function () {
        window.plus = window.plus
      }
      if (window.plus) {
        plusReady(this)
      } else {
        document.addEventListener('plusready', plusReady, false)
      }
    }
  }



