
  import utils from '../utils'
  import { mapGetters, mapActions } from 'vuex'
  import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

  import None from '../components/None'
  import TabelView from '../components/TabelView'
  export default {
    components: {
      XButton,
      None,
      Scroller,
      Spinner,
      TabelView
    },
    data () {
      return {
        status: {
          pulldownStatus: 'default'
        },
        downConf: { // 下拉配置
          content: 'Pull Down To Refresh',
          height: 60,
          autoRefresh: false,
          downContent: '下拉刷新',
          upContent: '加载中...',
          loadingContent: '加载中...',
          clsPrefix: 'xs-plugin-pulldown-'
        },
        state: true
      }
    },
    watch: { // 监听下拉下拉的状态
      status (e) {
        const $this = this
        if (e.pulldownStatus === 'loading') {  //下拉刷新
          if($this.state) {
            $this.state = false
	          setTimeout(() => {
	            $this.QueryStationChk(this.stationParams).then((res) => {
	              $this.state = true
	              $this.$nextTick(() => {
		              this.$refs.scroller.reset({
		                top: 0
		              })
		            })
	              $this.status.pulldownStatus = 'default'
	            })
	          }, 500)
          }else {
            $this.status.pulldownStatus = 'default'
          }
        }
      }
    },
    computed: {
      ...mapGetters({
        CurShiftInfo: 'CurShiftInfo',
        shifts: 'QueryChkShifts',
        StationChk: 'QueryStationChk'
      }),
      stationParams () {
        return {
          stationId: this.StationChk.stationId,
          shiftId: this.StationChk.shiftId
        }
      }
    },
    methods: {
      ...mapActions([
        'QueryChkShifts',
        'QueryRecenShifts',
        'QueryStationChk'
      ]),
      lookDetail () {
      	this.$router.push('/home/shiftdetail')
      },
      checkTicket () {
        this.$router.push('/home/ticketcheck')
      }
    },
    created () {
      let currentTime = utils.getCurrentTime()
      if(this.CurShiftInfo.departTime === null) {
      	// 获取最近一班次
      	let recenShifts = localStorage.getItem('recenShifts')
      	if(recenShifts !== null && recenShifts !== '' && recenShifts !== undefined) {
      	  recenShifts = JSON.parse(recenShifts)
      	  
      	  this.$store.commit('QueryRecenShifts', { res: recenShifts })
      	  this.$nextTick(() => {
		        this.$refs.scroller.reset()
		      })
      	  this.QueryStationChk(this.stationParams)
      	  
          let day2 = this.CurShiftInfo.departDate
      	  if(new Date(currentTime).getTime() > new Date(day2).getTime()) {
      	    localStorage.removeItem('recenShifts')
      	    this.QueryRecenShifts().then(res => {
      	      this.$nextTick(() => {
		            this.$refs.scroller.reset()
		          })
      	    })
      	  }
      	  
      	}else {
      		this.QueryRecenShifts().then(res => {
      		  this.$nextTick(() => {
		          this.$refs.scroller.reset()
		        })
      		})
      	}
      }else {
        this.QueryStationChk(this.stationParams)
      }
    }
  }



