
  import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

  export default {
    components: {
      Tab,
      TabItem
    },
    methods: {
      channgeTab (tab) {
      	if(this.selected !== tab) {
	        switch (tab) {
	          case 'home': this.$router.push('/')
	            break
	          case 'steward': this.$router.push('/steward/todayshift')
	            break
	          default: this.$router.push('/user')
	            break
	        }
        }
      }
    },
    computed: {
      selected () {
        if (this.$route.path === '/') {
          return 'home'
        }else if(/steward/.test(this.$route.path)) {
        	return 'steward'
        }else{
          return this.$route.path.replace('/', '')
        }
        
      }
    }
  }



