// 引入MD5加密库
const md5 = require('blueimp-md5')
export default {
	//请求参数配置
	requireData:function(postData) {
		if(postData !== undefined) {
		  let postDataStr = JSON.stringify(postData)
		  postData = JSON.parse(postDataStr)
		}
		/*拿取用户uid*/
		let driverInfo = localStorage.getItem('driverInfo')
	    let uid = '', phone = ''
		if(driverInfo==='undefined' || driverInfo===null || driverInfo === null) {
		  uid = 0
		}else {
		    driverInfo = JSON.parse(driverInfo)
		    uid= driverInfo.id
			phone = driverInfo.phone;
		}
		
		let oData= {
			handle:0,			//对提交的数据进行处理
			shandle:0,			//server是否对返回的数据进行处理
			data:{			
				phead:{								//请求的数据头信息
					pversion:1,
					uid: uid,				//用户id
					cid:3,
					cversion:1,
					cversionname:'5.3',
					channel:1,
					requesttime: new Date(),
					coordinates:'',
					positions:'',
				},
			},
		    pkey: 'gsfc_ticket_chk_driver',
		    sign: ''
		};
		if(postData instanceof Object){
			for(let x in postData) {
				oData.data[x]=postData[x];		//请求的数据信息
			}
		}
		let dataStr = JSON.stringify(oData.data)
		oData.sign = md5('gsfc@2017#driver$tChk' + dataStr + 'gsfc@2017#driver$tChk')
//		console.log(oData)
		return JSON.stringify(oData);
	},
	
    getCurrentTime (type) {  // 获取当前日期和时间
          const today=new Date()
	      let year = today.getFullYear()
	      let month = today.getMonth() + 1
	      let day = today.getDate()
		  let h=today.getHours()
		  let m=today.getMinutes()
		  let s=today.getSeconds()
		  m = m >= 10 ? m : '0' + m
		  s = s >= 10 ? s : '0' + s
		  month = month >= 10 ? month : '0'+month
	      day = day >= 10 ? day : '0'+day
		  if(type === 'time') {
		    return `${h}:${m}:${s}`
		  }else {
		    return `${year}-${month}-${day}`
		  }
    },
    filterDay (day) {
      let fday = day >= 10 ? day : '0'+day
      return fday
    },
    filterMonth (month) {
      let fmonth = month >= 10 ? month : '0'+month
      return fmonth
    },
    getRecentlyDate () { // 获取最近三天日期
      const newDate = new Date()
	  let year  =  newDate.getFullYear()
	  let month = newDate.getMonth() + 1
	  let day   =   newDate.getDate()
	  // 计算某年某月有多少天
	  let getDaysInMonth = new Date(year, month, 0).getDate()
	  let today,tomorrow,afterTomorrow,day1,day2,day3,month1,month2;
	  if(month!==12) {
		  if(day == getDaysInMonth) {
		  	
		  	month1 = this.filterMonth(month)
		  	month2 = this.filterMonth(month+1)
		  	today = `${year}-${month1}-${day}`
		  	tomorrow = `${year}-${month2}-01`
		  	afterTomorrow = `${year}-${month2}-02`
		  	
		  }else if(day + 1 == getDaysInMonth){
		  	
		  	month1 = this.filterMonth(month)
		  	month2 = this.filterMonth(month+1)
		  	today = `${year}-${month1}-${day}`
		  	tomorrow = `${year}-${month2}-${getDaysInMonth}`
		  	afterTomorrow = `${year}-${month2}-01`
		  	
		  }else {
		  	month = this.filterMonth(month)
		  	day1  = this.filterDay(day)
		  	day2  = this.filterDay(day+1)
		  	day3  = this.filterDay(day+2)
		  	today = `${year}-${month}-${day1}`
		  	tomorrow = `${year}-${month}-${day2}`
		  	afterTomorrow = `${year}-${month}-${day3}`
		  }
	  }else {
	      if(day == getDaysInMonth) {
		  	year = year + 1
		  	month1 = this.filterMonth(month)
		  	month2 = this.filterMonth(month+1)
		  	today = `${year}-${month1}-${day}`
		  	tomorrow = `${year}-${month2}-01`
		  	afterTomorrow = `${year}-${month2}-02`
		  	
		  }else if(day + 1 == getDaysInMonth){
		  	year = year + 1
		  	month1 = this.filterMonth(month)
		  	month2 = this.filterMonth(month+1)
		  	today = `${year}-${month1}-${day}`
		  	tomorrow = `${year}-${month2}-${getDaysInMonth}`
		  	afterTomorrow = `${year}-${month2}-01`
		  	
		  }else {
		  	month = this.filterMonth(month)
		  	day1  = this.filterDay(day)
		  	day2  = this.filterDay(day+1)
		  	day3  = this.filterDay(day+2)
		  	today = `${year}-${month1}-${day1}`
		  	tomorrow = `${year}-${month}-${day2}`
		  	afterTomorrow = `${year}-${month}-${day3}`
		  }
	  }
	  return {
	    today: today,
	    tomorrow: tomorrow,
	    afterTomorrow: afterTomorrow
	  }
	  
    }
}



