
  import mHeader from './components/header'
  import GpsControl from './components/GpsControl'
  import Tabs from './components/Tabs.vue'
  import ViewBox from 'vux/src/components/view-box/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

  export default {
    components: {
      mHeader,
      ViewBox,
      Tabs,
      LoadMore,
      GpsControl
    },
    computed: {
      direction () {
        return this.$store.state.comm.direction
      },
      isTabbarShow () {
        return this.$route.meta.showTabs
      },
      loadMore () {
        return this.$store.state.comm.loadMore
      }
    },
    created () {
      /*拿取用户uid*/
			let driverInfo = localStorage.getItem('driverInfo')
			if(driverInfo === undefined || driverInfo === 'undefined' || driverInfo === null) {
			  this.$router.replace('/user/login')
			}else {
				driverInfo = JSON.parse(driverInfo)
			  this.$store.state.responseData['DriverInfo'] = driverInfo
			}
    }
  }



