// 退出系统
export const GetLoginOut = ({commit, state}) => {
  commit('GetLoginOut')
}

// 登录系统
export const GetLogin = ({commit, state}, params) => {
	state.comm.loading = true
	params = utils.requireData(params)
  return api.GetLogin(params).then((res) => {
  	state.comm.loading = false
    res = res.data
  	commit('GetLogin', { res })
  	return res
  	
  }).catch((err) => {
    state.comm.loading = false
  })
}



