// 引入babel-polyfill 解决低版本安卓系统不支持 promise
import 'babel-polyfill'

import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'

// 引入路由配置
import router from './router'
// 引入vuex
import store from './store'

import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

Vue.use(ConfirmPlugin)
Vue.use(ToastPlugin)
Vue.use(AlertPlugin)

FastClick.attach(document.body)

import infiniteScroll from 'vue-infinite-scroll'
Vue.use(infiniteScroll)
// 实时定位 
getrealLocation()
function getrealLocation() {
	// 每  ${TIME_INT} 秒定位一次
    let TIME_INT = 15;
    let POINT = null;
    let successCount = 0;
    let errorCount = 0;
      let lastTime = new Date().getTime()
      let options = {
          enableHighAccuracy: true,
          maximumAge: 0,
          timeout: 60000
        }
        // 开启定位
      let watchID = navigator.geolocation.getCurrentPosition(onSuccess, onError, options)

      let map = new BMap.Map()

      function onSuccess(position) {
      	successCount++
        // 当前系统时间
        let currentTime = new Date().getTime()
        let coords = position.coords
        // 获取纬度
        let lat = coords.latitude
        // 获取经度
        let lon = coords.longitude
        // 获取方向，没有则设为0
        let heading = coords.heading || 0
        // 获取速度，没有则为0
        let speed = coords.speed || 0
        // 获取时间,没有则为本地时间
        let timestamp = coords.timestamp || currentTime
        
        let point = new BMap.Point(lon, lat)
        let distance = parseInt(map.getDistance(point, POINT))
        POINT = point
            
          //将h5得到的经纬度转化为百度地图的经纬度
          let geoc = new BMap.Geocoder()
          let convertor = new BMap.Convertor()
          let pointArr = []
          pointArr.push(point)
          convertor.translate(pointArr, 1, 5, function (data) {
            if (data.status === 0) {
              point = data.points[0]
              let driverId = store.state.responseData['DriverInfo'].id
              let _lat = (point.lat).toFixed(6)
              let _lng = (point.lng).toFixed(6)
              const pbStr = `${driverId},app,${_lat},${_lng},${coords.accuracy},${heading},${speed},${timestamp}`
              console.log(pbStr)
              // 上传坐标点
              if(driverId != undefined) {
                store.dispatch('SendPoint', pbStr)
              }
            }
          })

        // 记录当前坐标点与上一个坐标点的时间差
        let differTime = (currentTime - lastTime) / 1000
        console.log(differTime)
        const GpsControl = {
          lat: lat,
          lon: lon,
          heading: heading,
          speed: speed,
          currentTime: new Date().toLocaleString(),
          differTime: differTime,
          successCount: successCount,
          errorCount: errorCount
        }
        store.commit('GpsControl', {GpsControl: GpsControl});
        if (differTime < TIME_INT) {
//        setTimeout(function () {
            navigator.geolocation.getCurrentPosition(onSuccess, onError, options)
//        }, (TIME_INT - differTime) * 1000)
        } else {
          navigator.geolocation.getCurrentPosition(onSuccess, onError, options)
        }
        // 记录该点时间
        lastTime = new Date().getTime()
      }
      // 定位失败
      function onError(error) {
      	errorCount++;
        switch (error.code) {
          case error.PERMISSION_DENIED:
            console.log("User denied the request for Geolocation.")
            break;
          case error.POSITION_UNAVAILABLE:
            console.log("Location information is unavailable.")
            break;
          case error.TIMEOUT:
            console.log("The request to get user location timed out.")
            break;
          case error.UNKNOWN_ERROR:
            console.log("An unknown error occurred.")
            break;
        }
        setTimeout(() => {
          navigator.geolocation.getCurrentPosition(onSuccess, onError, options)
        }, 3000)
      }
}


/* eslint-disable no-new */
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app-box')



