
//const ORIGIN = 'http://192.168.2.121'
const ORIGIN = 'http://lysfc.imwork.net'
export default {
  // 1GPS定位信息上传接口
  SEND_POINT_URL: 'http://www.grandsea.com.cn' + '/gps',
 
  // 2二维码验票接口
  GET_QRCODE_CHK_URL: ORIGIN + '/sfc-ticket-check/qrcode_chk',
  
  // 3手动验票接口
  GET_HAND_CHK_URL: ORIGIN + '/sfc-ticket-check/hand_chk',
  
  // 统一验票接口，不用上面两个
  GET_TICKET_CHK_URL: ORIGIN + '/sfc-ticket-check/ticket_chk',
  
  // 3.1获取票务信息接口
  QUERY_TICKET_INFOS_URL: ORIGIN + '/sfc-ticket-check/ticket_infos',
  
  // 4获取验票班次接口
  QUERY_CHK_SHIFTS_URL: ORIGIN + '/sfc-ticket-check/chk_shifts',
  
  // 4.1获取当前验票班次接口
  QUERY_LATEST_SHIFTS_URL: ORIGIN + '/sfc-ticket-check/latest_shift',
  
  // 5获取站点即使验票情况接口
  QUERY_STATION_CHK_URL: ORIGIN + '/sfc-ticket-check/station_chk',
  
  // 6获取当前站点未验票乘客数据接口
  QUERY_UNCHKED_LIST_URL: ORIGIN + '/sfc-ticket-check/unchked_list',
  
  // 7获取班次统计接口
  QUERY_SHIFTS_STATIS_URL: ORIGIN + '/sfc-ticket-check/shifts_statis',
 
  // 8退出系统
  GET_LOGINOUT_URL: ORIGIN + '/sfc-ticket-check/chker_login',
  
  // 9登录系统
  GET_LOGIN_URL: ORIGIN + '/sfc-ticket-check/driver_login'
}



