import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
Vue.use(Router)

// 引入子页面
import Home from '../views/home.vue'
// 排班
const Steward = resolve => require(['../views/steward.vue'], resolve)
// 个人中心
const User = resolve => require(['../views/user.vue'], resolve)
// 更换站点
const ChangeStation = resolve => require(['../views/home/changeStation.vue'], resolve)
// 查看详情
const ShiftDetail = resolve => require(['../views/home/shiftDetail.vue'], resolve)
// 人工验票
const TicketCheck = resolve => require(['../views/home/ticketCheck.vue'], resolve)
// 更改班次
const ChangeShift = resolve => require(['../views/home/changeShift.vue'], resolve)
// 最近三天排班
const RecentlyShift = resolve => require(['../views/steward/recentlyShift.vue'], resolve)
// 班次统计
const Statistics = resolve => require(['../views/user/statistics.vue'], resolve)
// 登录
const Login = resolve => require(['../views/user/login.vue'], resolve)
// 二维码扫码
const QRcode = resolve => require(['../views/home/qrcode.vue'], resolve)
// 班次统计具体记录
const StatisRecord = resolve => require(['../views/user/statisRecord.vue'], resolve)

// 今天排班
const TodayShift = resolve => require(['../views/steward/todayShift.vue'], resolve)
// 明天排班
const TomoShift = resolve => require(['../views/steward/tomoShift.vue'], resolve)
// 后天排班
const AfterTomoShift = resolve => require(['../views/steward/afterTomoShift.vue'], resolve)

// 票务信息
const TicketInfos = resolve => require(['../views/home/TicketInfos.vue'], resolve)

const router =  new Router({
  routes: [
    {
	    path: '/',
	    component: Home,
	    meta: {
        title: '验票情况',
        isBack: false,
        showTabs: true
      }
	  },
	  {
	  	path: '/steward', 
	  	component: Steward, 
	  	meta: {
        title: '排班情况',
        isBack: false,
        showTabs: true
      },
     children: [
       { 
       	path: '/steward/todayshift', 
       	component: TodayShift,
       	meta: {
	        title: '排班情况',
	        isBack: false,
	        showTabs: true
	      }
       },
       { 
       	path: '/steward/tomoshift', 
       	component: TomoShift,
       	meta: {
	        title: '排班情况',
	        isBack: false,
	        showTabs: true
	      }
       },
       { 
       	path: '/steward/aftertomoshift', 
       	component: AfterTomoShift,
        meta: {
	        title: '排班情况',
	        isBack: false,
	        showTabs: true
	      }
       }
     ]
	  },
    {
    	path: '/user', 
    	component: User, 
    	meta: {
        title: '个人中心',
        isBack: false,
        showTabs: true
      }
    },
    {
    	path: '/home/changestation', 
    	component: ChangeStation, 
    	meta: {
        title: '更换站点',
        isBack: true,
        showTabs: false
      }
    },
    {
    	path: '/home/shiftdetail', 
    	component: ShiftDetail, 
    	meta: {
        title: '查看详情',
        isBack: true,
        showTabs: false
      }
    },
    {
    	path: '/home/ticketcheck', 
    	component: TicketCheck, 
    	meta: {
        title: '人工验票',
        isBack: true,
        showTabs: false
      }
    },
    {
    	path: '/home/ticketinfos', 
    	component: TicketInfos, 
    	meta: {
        title: '验票结果',
        isBack: true,
        showTabs: false
      }
    },
    {
    	path: '/home/changeshift', 
    	component: ChangeShift, 
    	meta: {
        title: '更改班次',
        isBack: true,
        showTabs: false
      }
    },
    {
    	path: '/steward/RecentlyShift', 
    	component: RecentlyShift, 
    	meta: {
        title: '最近三天排班',
        isBack: true,
        showTabs: false
      }
    },
    {
    	path: '/user/statistics', 
    	component: Statistics, 
    	meta: {
        title: '班次统计',
        isBack: true,
        showTabs: false
      }
    },
    {
    	path: '/user/statisrecord', 
    	component: StatisRecord, 
    	meta: {
        title: '具体记录',
        isBack: true,
        showTabs: false
      }
    },
    {
    	path: '/user/login', 
    	component: Login, 
    	meta: {
        title: '登录司机端',
        isBack: false,
        showTabs: false
      }
    },
    {
    	path: '/home/qrcode', 
    	component: QRcode, 
    	meta: {
        title: '二维码扫描',
        isBack: true,
        showTabs: false
      }
    }
  ]
})

// simple history management
const history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)

router.beforeEach((to, from, next) => {
  store.commit('isLoading', {isLoading: true})

  const toIndex = history.getItem(to.path)
  const fromIndex = history.getItem(from.path)
  if (toIndex) {
    if (toIndex > fromIndex || !fromIndex || (toIndex === '0' && fromIndex === '0')) {
      store.commit('updateDirection', {direction: 'forward'})
    } else {
      store.commit('updateDirection', {direction: 'reverse'})
    }
  } else {
    ++historyCount
    history.setItem('count', historyCount)
    to.path !== '/' && history.setItem(to.path, historyCount)
    store.commit('updateDirection', {direction: 'forward'})
  }

  if (/\/http/.test(to.path)) {
    let url = to.path.split('http')[1]
    window.location.href = `http${url}`
  } else {
    next()
  }
})

router.afterEach((to) => {
  store.commit('isLoading', {isLoading: false})
  store.state.comm.loadMore = 'success'
  store.commit('changeIndexConf', {
    isBack: to.meta.isBack,
    title: to.meta.title
  })
})

export default router



