import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import * as actions from './actions'
import * as getters from './getters'
import shifts from './modules/shifts'

Vue.use(Vuex)
// localStorage.removeItem('POINTS')
let POINTS = localStorage.getItem('POINTS')
if(POINTS === null) {
  POINTS = []
}else {
  POINTS = JSON.parse(POINTS)
}
const state = {
	GpsControl: {},
	Gpss: [],
  comm: {
    loading: false,
    loadMore: 'loading',
    direction: 'forward', // router transition 切换路由的动画
    tabSelected: 'Home',
    indexConf: {
      isFooter: true,
      isBack: true,
      title: '' // 标题
    }
  },
  POINTS: POINTS, // 保存轨迹坐标点
  responseData: { // 服务端接口返回的数据保存在这里
    'GetQrCodeChk': [],
    'GetHandChk': [],
    'QueryChkShifts': [],
    'QueryStationChk': {chkedTicketNum: 0, totalTicketNum: 0, stationId: 0, stationName: '', shiftId: 0}, //当前站点验票情况
    'QueryUnchkedList': [],
    'QueryShiftsStatis': [],
    'DriverInfo': [], //司机信息
    'CurShiftInfo': {departTime: null}, // 当前验票班次
    'QueryTicketInfos': []
  },
  // 统计传参
  statisParams: {
    fromDate: '',
    toDate: ''
  }
}
export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters,
  modules: {
    shifts
  }
})



