import * as api from '../../api/api'
import utils from '../../utils'
const today = utils.getRecentlyDate().today
const tomorrow = utils.getRecentlyDate().tomorrow
const afterTomorrow = utils.getRecentlyDate().afterTomorrow
const state = {
  todayShifts: {
    pageid: 1,
    list: []
  },
  tomoShifts: {
    pageid: 1,
    list: []
  },
  afterTomoShifts: {
    pageid: 1,
    list: []
  }
}

const getters = {
  todayShifts: state => state.todayShifts.list,
  tomoShifts: state => state.tomoShifts.list,
  afterTomoShifts: state => state.afterTomoShifts.list
}

const actions = {
  getTodayShifts ({ state, commit }) {
  	  let params = {
  	    ticketDate:  today,
  	    pageid: state.todayShifts.pageid
  	  }
      params = utils.requireData(params)
	  return api.QueryChkShifts(params).then((res) => {
	    res = res.data
	    commit('getTodayShifts', { res })
	    return res
	  }).catch((err) => {
	  })
  },
  getTomoShifts ({ commit, state }) {
  	  let params = {
  	    ticketDate:  tomorrow,
  	    pageid: state.tomoShifts.pageid
  	  }
      params = utils.requireData(params)
	  return api.QueryChkShifts(params).then((res) => {
	    res = res.data
	    commit('getTomoShifts', { res })
	    return res
	  }).catch((err) => {
	  })
  },
  getAfterTomoShifts ({ commit, state }) {
  	  let params = {
  	    ticketDate:  afterTomorrow,
  	    pageid: state.afterTomoShifts.pageid
  	  }
      params = utils.requireData(params)
	  return api.QueryChkShifts(params).then((res) => {
	    res = res.data
	    commit('getAfterTomoShifts', { res })
	    return res
	  }).catch((err) => {
	  })
  }
}

const mutations = {
  getTodayShifts (state, { res }) {
  	state.todayShifts.pageid++
  	let list = state.todayShifts.list
    state.todayShifts.list = list.concat(res.shiftInfos)
  },
  getTomoShifts (state, { res }) {
  	state.tomoShifts.pageid++
  	let list = state.tomoShifts.list
    state.tomoShifts.list = list.concat(res.shiftInfos)
  },
  getAfterTomoShifts (state, { res }) {
  	state.afterTomoShifts.pageid++
  	let list = state.afterTomoShifts.list
    state.afterTomoShifts.list = list.concat(res.shiftInfos)
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



